import jwt_decode from "jwt-decode";
import CysToast from '@/components/showToast/showToast.vue'
export default {
	components: {
		CysToast
	},
	data() {
		return {
			loginUser:{
				tel:"",
				password:""
			},
			msg: '',
			type: '',
			moveTypes: ''
		}
	},
	onLoad() {
		
	},
	methods: {
		gotoRegistration: function () {
			uni.navigateTo({url: '../registration/registration'});
		},
		gotoForgetPassword: function () {
			uni.navigateTo({url: '../forget-password/forget-password'});
		},
		login(){
			uni.request({
			    url: 'http://192.168.43.251:5001/api/user/login', 
			    data: this.loginUser,
				method:"POST",
			    success: (res) => {
					if(res.statusCode == 200){
						uni.showToast({
							title: "登陆成功"
						});
						const { token } = res.data;
						uni.setStorage({
						    key: 'scheduleToken',
						    data: token
						});
										
						// 解析token
						const decode = jwt_decode(token);
										
						// 存储数据
						this.$store.dispatch("setIsAutnenticated", !this.isEmpty(decode));
						this.$store.dispatch("setUser", decode);
						setTimeout( () => {
							uni.switchTab({url: '../home/home'});
						},1000);
					}
					else if(res.statusCode == 400 || res.statusCode == 404){
						this.moveTypes = 'bottom'
						this.type = 'fail'
						this.msg = res.data
						let that = this
						setTimeout(function() {
							that.moveTypes = ''
						}, 1500)
					}
			    }
			});
		},
		isEmpty(value) {
		  return (
		    value === undefined ||
		    value === null ||
		    (typeof value === "object" && Object.keys(value).length === 0) ||
		    (typeof value === "string" && value.trim().length === 0)
		  );
		},
		reduction() {
			this.moveTypes = ''
		},
	}
}
